import React, { useState, useEffect } from "react";
import SectionHead from "../../components/SectionHead";
import { useParams } from "react-router-dom";
import "./updateProductAdmin.css";

function ProductForm() {
  const { productId } = useParams(); // Extrage ID-ul produsului din URL
  const [feedbackDesc, setFeedbackDesc] = useState("");
  const [feedbackName, setFeedbackName] = useState("");
  const [feedbackImg, setFeedbackImg] = useState("");
  const [product, setProduct] = useState({
    name: "",
    price: "",
    description: "",
    image: null,
    imageUrl: null,
  });

  // Definirea funciei fetchProduct direct n component
  const fetchProduct = async (productId) => {
    try {
      const response = await fetch(
        `http://localhost:8060/public/product/${productId}`
      );
      if (!response.ok) {
        throw new Error("Failed to fetch product");
      }
      const data = await response.json(); // Asigur-te c 'data' este definit nainte de a fi folosit
      console.log(data); // Logheaz 'data' pentru a verifica coninutul su
      return data;
    } catch (error) {
      console.error("Error fetching product:", error);
      throw error; // Propag eroarea pentru a putea fi prins mai departe
    }
  };

  // Funcie pentru ncrcarea datelor produsului
  useEffect(() => {
    if (productId) {
      fetchProduct(productId)
        .then((data) => {
          setProduct({
            name: data.name,
            price: data.price,
            description: data.description,
            image: data.image,
            imageUrl: `http://localhost:8060${data.image}`,
          });
        })
        .catch((error) => {
          console.error("Failed to load product:", error);
          alert("Failed to load product data");
        });
    }
  }, [productId]); // 'productId' ca dependin pentru a rencrca datele la schimbarea ID-ului

  const handleChange = (e) => {
    const { name, value } = e.target;
    setProduct({ ...product, [name]: value });

    // Actualizm feedback-ul pentru fiecare cmp
    if (name === "description") {
      const maxLength = 250;
      const currentLength = value.length;
      setFeedbackDesc(`${maxLength - currentLength} characters left`);
    } else if (name === "name") {
      const maxLength = 20;
      const currentLength = value.length;
      setFeedbackName(`${maxLength - currentLength} characters left`);
    }
  };

  // Manipuleaz schimbrile fiierului de imagine
  const handleFileChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (ev) => {
        const img = new Image();
        img.onload = () => {
          if (img.width === 452 && img.height === 552) {
            setProduct((prev) => ({ ...prev, image: file }));
            setFeedbackImg(""); // Reseteaz mesajul de eroare dac dimensiunile sunt corecte
          } else {
            setProduct((prev) => ({ ...prev, image: null })); // Asigur-te c resetezi imaginea
            setFeedbackImg("Image must be 452x552 pixels.");
          }
        };
        img.src = ev.target.result;
      };
      reader.readAsDataURL(file);
    } else {
      setProduct((prev) => ({ ...prev, image: null })); // Reseteaz imaginea dac niciun fiier nu este selectat
      setFeedbackImg(""); // Reseteaz mesajul de eroare
    }
  };

  // Manipuleaz trimiterea formularului
  // Manipuleaz trimiterea formularului
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!product.image || feedbackImg == "Image must be 452x552 pixels.") {
      alert("Please fix the errors before submitting.");
      return;
    }
    const formData = new FormData();
    formData.append(
      "product",
      JSON.stringify({
        name: product.name,
        price: product.price,
        description: product.description,
      })
    );

    if (product.image) {
      formData.append("image", product.image);
    }
    const token = localStorage.getItem("token");

    try {
      const response = await fetch(
        `http://localhost:8060/admin/update/${productId}`,
        {
          method: "PUT",
          body: formData,
          headers: {
            // Include tokenul JWT n headers pentru autentificare
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (!response.ok) {
        throw new Error("Something went wrong");
      }
      const updatedProduct = await response.json();
      alert("Product updated successfully");
      console.log(updatedProduct);
    } catch (error) {
      console.error("Failed to update the product:", error);
    }
  };

  return (
    <section className="updateProduct">
      <SectionHead title="UP PPRODUCT" subtitle="UP PRODUCT" />
      <div className="container_updatePorductAdmin">
        <form onSubmit={handleSubmit}>
          <label>
            Name:
            <input
              maxLength="20"
              className="nameInput"
              type="text"
              name="name"
              value={product.name}
              onChange={handleChange}
            />
          </label>
          <div className="feedbackName">{feedbackName}</div> {}
          <label>
            Price:
            <input
              className="priceInput"
              type="text"
              name="price"
              value={product.price}
              onChange={handleChange}
            />
          </label>
          <label className="desc">
            Description:
            <textarea
              maxLength="250"
              className="descInput"
              name="description"
              value={product.description}
              onChange={handleChange}
            />
          </label>
          <div className="feedbackDesc">{feedbackDesc}</div> {}
          <label>
            Image:
            <input
              className="imgInput"
              type="file"
              name="image"
              onChange={handleFileChange}
            />
          </label>
          <div className="feedbackImg">{feedbackImg}</div> {}
          <button className="UpdateProductBt" type="submit">
            Update Product
          </button>
        </form>
        <div className="CurretnImage">
          <p>Current Image:</p>
          {product.imageUrl && (
            <img src={product.imageUrl} alt="Current Product" />
          )}
        </div>
      </div>
    </section>
  );
}

export default ProductForm;
